@charset "utf-8";
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap");
// font-family: 'Source Sans Pro', sans-serif;
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
//font-family: 'Roboto', sans-serif;
@import "base/setting";

/* =======================================
	Common Style
======================================= */
html * {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-text-size-adjust: 100%;
}

html {
	overflow: auto;
	height: 100%;
}

html,
body {
	position: relative;
	-webkit-overflow-scrolling: touch;
	color: #43425d;
	font-size: $font-size-medium;
	font-family: $font-family-base;
	line-height: 1.6rem;
}

body {
	right: 0;
	left: 0;
	margin: auto;
	max-width: 768px;
	background: $color-back;
	color: #43425d;
}

*:focus {
	outline: none;
}

* {
	-webkit-appearance: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, .1);
}

a {
	color: #43425d;
	text-decoration: none;
}

header {
	position: relative;
	margin: auto;
	padding: 1rem 0 0 4rem;
	max-width: 768px;
	width: 100%;
	h1 {
		margin: 0 auto 1.5rem;
		color: #11b1dc;
		img {
			height: 2.5rem;
		}
	}
	.side-menu {
		position: absolute;
		top: 0;
		left: 0;
		.hamburger {
			position: absolute;
			top: 13px;
			left: 10px;
			padding: 10px;
			width: 50px;
			height: 50px;
			cursor: pointer;
			.line {
				display: block;
				margin: 4px auto;
				width: 30px;
				height: 4px;
				background-color: #11b1dc;
				transition: all .3s ease-in-out;
			}
		}
		.menu-list {
			display: none;
		}
		&.show,
		&:hover {
			.hamburger {
				.line:nth-child(1),
				.line:nth-child(3) {
					width: 25px;
				}
				.line:nth-child(1) {
					transform: translateX(-10px) rotate(-45deg);
				}
				.line:nth-child(3) {
					transform: translateX(-10px) rotate(45deg);
				}
			}
		}
	}
}

// Typography
h1.title-logo {
	margin: 0 auto 50px;
	max-width: 200px;
	width: 100%;
}

h1 {
	margin: 0 auto .5rem;
	color: #11b1dc;
	font-weight: bold;
	font-size: $font-size-giant;
}

.text-medium {
	margin: 0 auto .25rem;
	font-size: $font-size-medium;
	line-height: 26px;
}

.text-small {
	margin: 0 auto .25rem;
	font-size: $font-size-small;
	line-height: 22px;
}

.center {
	text-align: center;
}

.small {
	font-size: $font-size-slight;
}

.bold {
	font-weight: bold;
}

//CONTENT
.container {
	margin: 3rem auto 0;
	max-width: 768px;
	width: 100%;
}

//CARD
.card {
	margin: auto;
	padding: 2rem 1rem .5rem;
	max-width: 580px;
	width: calc(100% - 2rem);
	border-radius: 10px;

	&.white {
		background: #fff;
		box-shadow: 0 -3px 20px rgba(#c3e8f7, .7);
	}
}

//list presents
.list-presents {
	margin: 0 auto 1.5rem;
	width: 100%;

	> dt {
		position: relative;
		padding: .5rem 1.5rem;
		width: 80%;
		border-radius: 0 2rem 2rem 0;
		background: $color-blue-gr;
		color: #fff;
		font-weight: bold;
		font-size: $font-size-giant;
		font-family: "Roboto", sans-serif;
		cursor: pointer;

		&::before,
		&::after {
			position: absolute;
			top: 45%;
			right: 20px;
			display: inline-block;
			width: 25px;
			height: 4px;
			background: #fff;
			content: "";
			text-align: center;
			transition: all 200ms;
		}
		&::after {
			transform: rotate(90deg);
		}

		&.show {
			&::before {
				content: "";
				opacity: 0;
			}
			&::after {
				transform: rotate(0deg);
			}
		}
	}

	> dd {
		position: relative;
		visibility: hidden;
		overflow: hidden;
		transition: height 600ms ease-in-out;
		animation: 400ms ease-in both fadeout;

		&.show {
			display: block;
			width: 100%;
			height: auto;
			animation: 600ms ease-out both fadein;
		}

		dl:last-of-type {
			margin: 0 auto;
		}
	}
}

@keyframes fadein {
	0% {
		visibility: hidden;
		padding: 0 1rem 0 3rem;
		opacity: 0;
	}

	100% {
		visibility: visible;
		padding: 1.5rem 1rem 1rem 3rem;
		opacity: 1;
	}
}

@keyframes fadeout {
	0% {
		visibility: visible;
		padding: 1.5rem 1rem 1rem 3rem;
		opacity: 1;
	}

	100% {
		visibility: hidden;
		padding: 0 1rem 0 3rem;
		opacity: 0;
	}
}


.card-gift {
	position: relative;
	margin: 0 auto 1rem;
	padding: 1rem;
	min-height: 115px;
	width: 100%;
	border-radius: 4px;
	background: #fff;
	box-shadow: 3px 3px 10px rgba(#c3e8f7, .7);

	&::before {
		position: absolute;
		top: 0;
		bottom: 0;
		left: -1.5rem;
		margin: auto;
		width: 1.5rem;
		height: 2px;
		background: rgba(#11b1dc, .6);
		content: "";
	}

	&::after {
		position: absolute;
		top: calc(100% - 150% - 1.5rem);
		left: -1.5rem;
		margin: auto;
		width: 2px;
		height: calc(100% + 1rem);
		background: rgba(#11b1dc, .6);
		content: "";
	}

	.item-area {
		position: absolute;
		top: 1rem;
		text-align: center;

		.date {
			display: block;
			margin: 0 auto .5rem;
			color: #11b1dc;
			font-weight: bold;
			font-size: $font-size-large;
		}

		figure {
			overflow: hidden;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			background-image: linear-gradient(to bottom right, rgba(255,255,255,0) 0%, rgba(255,255,255,.7) 100% );
			box-shadow: 3px 3px 10px rgba(#11b1dc, .2);
			text-align: center;

			img {
				width: 100%;
				height: auto;
			}
		}

		a {
			display: block;
			transition: opacity 300ms ease-out;

			&:hover {
				opacity: .8;
			}
		}
	}

	&.give {
		padding-left: 5rem;

		.item-area {
			left: 1rem;
		}
	}

	&.take {
		padding-right: 5rem;

		.item-area {
			right: 1.5rem;
		}
	}

	> dt {
		margin: 0 auto .25rem;
		font-weight: bold;
		font-size: $font-size-medium;

		&::before {
			position: absolute;
			top: 0;
			bottom: 0;
			left: -2.1rem;
			z-index: 10;
			display: block;
			margin: auto;
			width: 20px;
			height: 20px;
			border: 4px solid #11b1dc;
			border-radius: 50%;
			background: $color-back;
			content: "";
		}
		a {
			color: #0c93c9;
		}
	}
}

//line
hr {
	margin: 0 auto 1.5rem;
	border: none;

	&.dot_line {
		border: 1px dashed rgba(#11b1dc, .2);
	}
}


/* =======================================
	Components
======================================= */
//BUTTON
.btn {
	position: relative;
	z-index: 0;
	display: block;
	margin: 0 auto 1.5rem;
	max-width: 248px;
	width: 100%;
	height: 48px;
	border-radius: 25px;
	color: #fff;
	text-align: center;
	font-weight: bold;
	font-size: 1.1rem;
	line-height: 48px;

	&::before,
	&::after {
		position: absolute;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		border-radius: 25px;
		content: "";
		transition: 300ms ease-out;
	}

	&:hover {
		&::before {
			opacity: 0;
		}
	}

	&:active {
		&::after {
			opacity: 0;
		}
	}
}

.btn-primary {
	background: #11b1dc;

	&::before {
		background: $color-blue-gr;
	}

	&::after {
		box-shadow: 3px 3px 10px rgba(#0c93c9, .6);
	}

	&:active {
		box-shadow: 1px 1px 10px #0c93c9 inset;
	}
}

.btn-info {
	background: #fe719d;

	&::before {
		background: $color-pink-gr;
	}

	&::after {
		box-shadow: 3px 3px 10px rgba(#af3781, .6);
	}

	&:active {
		box-shadow: 1px 1px 10px #af3781 inset;
	}
}

.btn-filter {
	background: #fff;
	color: #0c93c9;

	&::before {
		background: linear-gradient(to left, rgba(#fff,1) 0%, rgba(#a7a7a7,.1) 100% );
	}

	&::after {
		box-shadow: 3px 3px 10px rgba(#0c93c9, .6);
	}

	&:active {
		box-shadow: 1px 1px 10px rgba(#0c93c9,.6) inset;
	}
}

// Link
.link-text {
	position: relative;
	display: inline-block;
	overflow: hidden;
	margin: 0 auto 1.5rem;
	line-height: 1.2rem;

	// パスワード忘れた人
	&.forget {
		padding: .1rem 0 .1rem 1.9rem;
		line-height: 1.2rem;
		&::before {
			position: absolute;
			top: 0;
			bottom: 0;
			left: .3rem;
			display: inline-block;
			margin: auto;
			width: 1.2rem;
			height: 1.2rem;
			border-radius: 50%;
			background: #fe719d;
			color: #fff;
			content: "？";
			text-align: center;
			line-height: 1.2rem;
		}
	}

	// 戻る
	&.back {
		&::before {
			display: inline-block;
			padding: 0 .3rem 0 0;
			color: #11b1dc;
		}
	}
}

// formgroup
.form-group {
	margin: 0 auto 1.5rem;
	max-width: 480px;
	text-align: left;

	span {
		display: block;
		font-size: $font-size-slight;
	}

	input {
		padding: 1rem .5rem;
		width: 100%;
		height: 48px;
		border: 1px solid #d1d1d1;
		border-radius: 4px;
		background: #fff;
		font-size: 1rem;
		line-height: 1rem;

		&::placeholder {
			padding: .1rem 0 0 0;
			color: #cecfd0;
			font-size: $font-size-small;
		}

		&:focus {
			border: 1px solid #a7a7a7;
			transition: 300ms ease-out;
		}
	}
}

// SPINNERS
.spinner {
	display: block;
	margin: 4rem auto;
	width: 3rem;
	height: 3rem;
	border: 3px solid transparent;
	border-top-color: rgba(#11b1dc, .5);
	border-bottom-color: rgba(#11b1dc, .5);
	border-radius: 50%;
	animation: rotation .8s ease infinite;
}

// ANIMATIONS
@keyframes rotation {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}

// badge
.badge-area {
	padding: .25rem 0 0;
	.badge {
		margin: 0 .1rem .2rem;
	}
}
.badge {
	display: inline-block;
	padding: .3rem .6rem;
	background: #11b1dc;
	color: #fff;
	vertical-align: baseline;
	text-align: center;
	white-space: nowrap;
	font-weight: 700;
	font-size: 75%;
	line-height: 1;
}

.badge-pill {
	border-radius: 10rem;
}

.fas::before {
	padding-right: .5rem;
}

// Search
#search {
	position: absolute;
	top: 0;
	right: 0;
	display: block;
	overflow: hidden;
	width: 80px;
	height: 80px;
	font-size: 0;
	cursor: pointer;
	pointer-events: auto;

	&::before {
		position: absolute;
		z-index: 1;
		display: block;
		padding: 0;
		width: 90px;
		color: #fff;
		text-align: center;
		font-size: 30px;
		line-height: 70px;
	}

	&::after {
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 0;
		display: block;
		padding: 0;
		width: 100px;
		height: 100px;
		border-radius: 50px;
		background: #11b1dc;
		color: #fff;
		content: "";
		text-align: center;
		font-size: 30px;
		line-height: 50px;
		transition: all 300ms ease-out;
	}

	&.show {
		&::before {
			content: "✕";
		}
		&::after {
		background-color: rgba(#11b1dc,0);
		transition: background-color 200ms;
		}
	}

	&:hover {
		&::after {
		background-color: rgba(#11b1dc,1);
		}
	}
}


.search-wrap {
	position: absolute;
	top: 0;
	right: 0;
	z-index: 99;
	display: block;
	overflow: hidden;
	padding: 50px 0 100px;
	min-height: 100vh;
	width: 100%;
	height: 100%;
	pointer-events: none;
	background-color: rgba(#11b1dc,0);
	&::after {
		position: absolute;
		top: -20px;
		right: -20px;
		z-index: 0;
		display: block;
		border-radius: 100%;
		background: #11b1dc;
		content: "";
		pointer-events: auto;
	}
	&.show {
		pointer-events: auto;
		background-color: rgba(#11b1dc,1);
		transition: background-color 100ms 400ms;
		overflow-y: auto;
		&::after {
			animation: 600ms ease-out both 50ms search;
		}
	}
	dl,
	.btn {
		position: relative;
		z-index: 1;
		margin: 50px auto 0;
		width: 80%;
		opacity: 0;
		dt {
			padding: 0 0 .5rem;
			color: #fff;
			font-size: 1.2rem;
		}
		dd {
			padding: .7rem 1rem .6rem;
			border-radius: 10px;
			background: #fff;
		}
	}
	.show dl {
		animation: 300ms ease-out both 600ms fadeArea;
		pointer-events: auto;
	}
	.btn.show {
		animation: 300ms ease-out both 600ms fadeArea;
		pointer-events: auto;
	}
}


@keyframes search {
	0% {
		top: -20px;
		right: -20px;
		width: 100px;
		height: 100px;
		opacity: .5;
	}
	90% {
		top: -500px;
		right: -500px;
		width: 2000px;
		height: 2000px;
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes fadeArea {
	0% {
		visibility: hidden;
		opacity: 0;
	}

	100% {
		visibility: visible;
		opacity: 1;
	}
}
